import React, { useReducer } from 'react';
import axios from 'axios';
import ContactContext from './ContactContext';
import ContactReducer from './ContactReducer';
import {
    ADD_CONTACT,
    CONTACT_ERROR,
    DELETE_CONTACT,
    SET_CURRENT,
    CLEAR_CURRENT,
    UPDATE_CONTACT,
    FILTER_CONTACTS,
    CLEAR_FILTER,
    GET_CONTACTS,
    CLEAR_CONTACTS
} from '../types';

const ContactState = props => {
    const initialState = {
        contacts: null,
        current: null,
        filtered: null,
        error: null
    }

    const [state, dispatch] = useReducer(ContactReducer, initialState);

    //Get Contacts
    const getContacts = async() => {
        try {
            const res = await axios.get('http://localhost:7000/api/contacts');
            dispatch({ type: GET_CONTACTS, payload: res.data });

        } catch (err) {
            dispatch({ type: CONTACT_ERROR, playload: err.response.msg });
        }
    }

    //Add Contact
    const addContact = async(contact) => {
        const config = {
            headers: {
                'Content-Type': 'application/json'
            }
        }

        try {
            const res = await axios.post('http://localhost:7000/api/contacts', contact, config);
            dispatch({ type: ADD_CONTACT, payload: res.data });

        } catch (err) {
            dispatch({ type: CONTACT_ERROR, playload: err.response.msg });
        }
    }

    //Delete Contact
    const deleteContact = async(id) => {
        try {
            await axios.delete(`http://localhost:7000/api/contacts/${id}`);
            dispatch({ type: DELETE_CONTACT, payload: id });

        } catch (err) {
            dispatch({ type: CONTACT_ERROR, playload: err.response.msg });
        }
        
    }

    //Update Contact
    const updateContact = async(contact) => {
        const config = {
            headers: {
                'Content-Type': 'application/json'
            }
        }

        try {
            const res = await axios.put(`http://localhost:7000/api/contacts/${contact._id}`, contact, config);
            dispatch({ type: UPDATE_CONTACT, payload: res.data });

        } catch (err) {
            dispatch({ type: CONTACT_ERROR, playload: err.response.msg });
        }
        
    }

    //Clear Contacts
    const clearContacts = () => {
        dispatch({ type: CLEAR_CONTACTS });
    }

    //Set Current Contact
    const setCurrent = contact => {
        dispatch({ type: SET_CURRENT, payload: contact });
    }

    //Clear Current Contact
    const clearCurrent = () => {
        dispatch({ type: CLEAR_CURRENT });
    }

    //Filter Contacts
    const filterContacts = text => {
        dispatch({ type: FILTER_CONTACTS, payload: text });
    }
    //Clear Filter
    const clearFilter = () => {
        dispatch({ type: CLEAR_FILTER });
    }

    return (
        <ContactContext.Provider
            value={{
                contacts: state.contacts,
                current: state.current,
                filtered: state.filtered,
                error: state.error,
                addContact,
                deleteContact,
                setCurrent,
                clearCurrent,
                updateContact,
                filterContacts,
                clearFilter,
                getContacts,
                clearContacts
            }}
        >
            { props.children }
        </ContactContext.Provider>
    )
}

export default ContactState;